/* eslint-disable import/no-anonymous-default-export */
import React from "react";
import { Title } from "components";
import { TextField } from "components/Formik";
import { Paper, Divider, Grid, Box, Button } from "@material-ui/core";

import { Formik, Form } from "formik";
import { useSnackbar, withSnackbar } from "notistack";

import { findClienteCnpj } from "shared/services/CNPJService";

import "./styles.scss";

function FormCNPJ() {
  const { enqueueSnackbar } = useSnackbar();

  const initialValues = {
    cpfCnpj: "",
  };

  const handleSubmit = async (values) => {
    if (values.cpfCnpj !== "") {
      handleFindCpfCnpj(values);
    } else {
      enqueueSnackbar("Adicione as informaçãoes obrigatórias.", {
        variant: "warning",
      });
    }
  };

  const handleFindCpfCnpj = async (cpfCnpj, updateField) => {
    const code = cpfCnpj;

    if (!code.length) return;

    try {
      const method = await findClienteCnpj(code);

      if (method) {
        updateField("nomeCliente", method.nome);
      } else {
        enqueueSnackbar("Cliente não encontrado", { variant: "error" });
      }
    } catch (e) {
      enqueueSnackbar("Cliente não encontrado", { variant: "error" });
    }
  };

  return (
    <Box mt={2} mb={2}>
      <Paper>
        <Box className="headerPaper">
          <Title>HELLO WORD </Title>
        </Box>
        <Divider />
        <Formik
          enableReinitialize={true}
          initialValues={initialValues}
          onSubmit={handleSubmit}
        >
          {({ setFieldValue, submitForm, resetForm }) => (
            <Form id="cnpj-form">
              <Box p={2} className="headerPaper">
                <Grid container spacing={2}>
                  <Grid item xs={4}>
                    <TextField
                      required
                      autoComplete="off"
                      type="text"
                      inputProps={{ maxLength: 18 }}
                      name="cpfCnpj"
                      label="CPF/CNPJ"
                      onKeyUp={(e) => {
                        if (e.key === "Enter") {
                          handleFindCpfCnpj(e, setFieldValue);
                        }
                      }}
                    />
                  </Grid>
                  <Grid className="prodField" item xs={11}>
                    <Button
                      variant="contained"
                      color="primary"
                      className="buttonCNPJ"
                      onClick={submitForm}
                    >
                      pesquisar
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Form>
          )}
        </Formik>
      </Paper>
    </Box>
  );
}

export default withSnackbar(FormCNPJ);
